#_1HKcN {
  display: flex;
  position: fixed;
  align-items: center;
  width: calc(100vh - 40px);
  padding: 10px 20px;
  transform: rotate(-90deg);
  top: 100%;
  transform-origin: left top;
  z-index: 2;
}

#_1HKcN > span > a {
  text-decoration: none;
  color: black;
  cursor: pointer;
}

#_1HKcN > span:nth-child(1) {
  font-size: 2em;
  font-weight: bolder;
}

#_1HKcN > span:nth-child(n+2) {
  margin-left: 10px;
  display: block;
  position: relative;
  text-transform: uppercase;
  font-size: 1em;
}

#_1HKcN > span:nth-child(2) {
  margin-left: 30px;
}

#_1HKcN > span:nth-child(5) {
  flex: 1;
  text-align: right;
}

#_1HKcN > span:nth-child(5) > i {
  transform: rotate(90deg);
  transform-origin: center;
  margin-left: 10px;
}

#_1HKcN > span:not(:first-child):not(:last-child)::after {
  content: '';
  background: black;
  transform-origin: left;
  transform: scale(0);
  transition: transform 500ms ease-in-out;
  height: 1px;
  width: 25px;
  display: block;
  position: absolute;
}

#_1HKcN > span:not(:first-child):not(:last-child):hover::after{
  transform: scale(1);
}

#_3hetG {
  min-height: 170px;
}

.k2yoa {
  padding-left: .5rem !important;
}

@media (min-width: 768px) { 
  #_1HKcN {
    width: calc(100vw - 15px);
    transform: rotate(0deg);
    top: 0%;
    position: absolute;
  }

  #_1HKcN > span:nth-child(5) > i {
    transform: rotate(0deg);
    cursor: pointer;
    margin-left: 25px;
  }
}